import React from 'react';
import { NavLink } from 'react-router-dom';
import web from '../src/images/img1.jpg';

const Common=(props)=>{
    return(
        <>
         <section id="header" className="d-flex align-items-center">
         <div className="container-fluid ">
            <div className='row'>
            <div className="col-10 mx-auto">
            <div className="row">
            <div className="col-md-6 pt-5 pt-lf-0 order-lg-1 d-flex justify-content-center flex-column">
                
                <h1>{props.name}
                <strong className="brand-name">

                <br/>PraveshStark</strong></h1><hr/>
                <h3 className="my-3">
                    Everyone have unique madeness
                </h3>
                <div className="mt-3">
                <NavLink class="btn btn-get-started" to={props.visit} >
                {props.btname}
                 </NavLink>
                </div>
                </div>
               
               <div className="col-lg-6 order-1 order-lg-2 header-img">
                    <img src={props.imgsrc} className="img-fluid animated" alt="Common img" />
        
                </div>
            </div>
          
            </div>
	        </div>
	        </div>         
         </section>
        </>
    );
};


export default Common;

